body {
  text-align: center;
  background-color: #ffebef;
  margin: 0;
  padding: 20px;
  font-family: Poppins, sans-serif;
}

h1 {
  text-align: left;
  color: #c2185b;
  margin-left: 40px;
  font-family: Brush Script MT, cursive;
  font-size: 4rem;
}

nav {
  text-align: left;
  margin-bottom: 20px;
  padding-left: 40px;
}

nav a {
  color: #c2185b;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 1.2rem;
  font-weight: bold;
  text-decoration: none;
  transition: all .3s;
}

nav a:hover {
  color: #7a0b3b;
  background-color: #f8bbd0;
}

#habit-container {
  width: 50%;
  margin: 30px auto;
}

#habitForm {
  justify-content: center;
  gap: 10px;
  display: flex;
}

#habitInput {
  border: 2px solid #c2185b;
  border-radius: 25px;
  outline: none;
  width: 60%;
  padding: 12px;
  font-size: 1rem;
  transition: all .3s;
}

#habitInput:focus {
  border-color: #7a0b3b;
  box-shadow: 0 0 10px #c2185b66;
}

#habitList {
  margin-top: 20px;
  padding: 0;
  list-style-type: none;
}

#habitList li {
  background: #fff;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  margin: 10px auto;
  padding: 15px;
  transition: all .3s;
  display: flex;
  box-shadow: 2px 2px 10px #0000001a;
}

#habitList li:hover {
  transform: scale(1.02);
}

.button-group {
  gap: 10px;
  display: flex;
}

button {
  color: #fff;
  cursor: pointer;
  background: #c2185b;
  border: none;
  border-radius: 8px;
  padding: 8px 15px;
  font-size: .9rem;
  transition: all .3s;
}

button:hover {
  background: #7a0b3b;
}

.edit-button, .delete-button, .save-button {
  padding: 6px 12px;
  font-size: .85rem;
}

.habit-text {
  color: #7a0b3b;
  font-size: 1.2rem;
  font-weight: bold;
}

#qrCode img {
  border-radius: 10px;
  margin-top: 15px;
}

#qrButton, #authButton {
  margin: 15px auto;
  display: block;
}

.message {
  color: #fff;
  background: #c2185b;
  border-radius: 25px;
  padding: 12px 20px;
  font-weight: bold;
  animation: 2s ease-in-out fadeInOut;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 0 10px #0003;
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
